
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default{
components: {
XHeader,Toast
},
data(){
return {
noData: false,
list: [],
isloading: false,
curIndex: 0,
}
},
mounted(){
this.initList();
},
methods: {
pullingUp(){






















},
PEdetail(o){
console.log('222222:'+JSON.stringify(o))

switch (o.DATA_TYPE) {
case 0:
this.$router.push({name:'NewbornPE',params:{ID:o.ID}});
break;
case 1:
this.$router.push({name:'F1T8MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 2:
this.$router.push({name:'F1T8MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 3:
this.$router.push({name:'F1T8MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 4:
this.$router.push({name:'F1T8MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 5:
this.$router.push({name:'F12T30MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 6:
this.$router.push({name:'F12T30MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 7:
this.$router.push({name:'F12T30MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 8:
this.$router.push({name:'F12T30MonthsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 9:
this.$router.push({name:'F3T6YearsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 10:
this.$router.push({name:'F3T6YearsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 11:
this.$router.push({name:'F3T6YearsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;
case 12:
this.$router.push({name:'F3T6YearsPE',params:{ID:o.ID,DATA_TYPE:o.DATA_TYPE,NAME:o.NAME}});
break;

default:

break;
}
},
initList(){


this.$ajax.post('',{
'MSH.1':'childrenExamService',
'MSH.2':'list',
'CHZBM':this.$root.loginInfo.CHZBM,
'FILTER_TYPE':"0",
'GRDAID':this.$root.loginInfo.grdaid,
'token':this.$root.loginInfo.token,
}).then(res => {
if(res && res.length > 0){

this.list = res;

if(this.list.length == 0){
this.noData = true;
}else{
this.noData = false;
}
}else{
if(this.list.length == 0){
this.noData = true;
}else{
this.noData = false;
}
this.isEmpty = true;
}
}).catch(error => {
console.log(error);

});
},

}
}



